"use client";
import React, { useState, useEffect, useRef } from "react";
import { Capacitor } from "@capacitor/core";
import { BleClient, Device } from "@capacitor-community/bluetooth-le";
import dynamic from 'next/dynamic';

// Dynamically import PianoTutorial to ensure it's only rendered on the client side
const PianoTutorial = dynamic(() => import('./PianoTutorial'), { ssr: false });

const MIDI_SERVICE_UUID = "03b80e5a-ede8-4b33-a751-6ce34ec4c700";
const MIDI_CHARACTERISTIC_UUID = "7772e5db-3868-4112-a1a9-f2669d106bf3";

const MidiPianoScanner = () => {
  const [scanResults, setScanResults] = useState<Device[]>([]);
  const [connectedDevice, setConnectedDevice] = useState<Device | null>(null);
  const [log, setLog] = useState("");
  const midiMessageCallbackRef = useRef<(data: Uint8Array) => void>();

  const logMsg = (msg: string) => setLog((prev) => prev + msg + "\n");

  useEffect(() => {
    if (typeof window !== "undefined" && Capacitor.isNativePlatform()) {
      BleClient.initialize()
        .then(() => logMsg("‚úÖ BLE initialized"))
        .catch((e) => logMsg("‚ö†Ô∏è Init error: " + e.message));
    } else {
      logMsg("üåê Running in browser mode (Web Bluetooth API)");
    }
  }, []);

  const handleMidiMessage = (data: Uint8Array) => {
    if (midiMessageCallbackRef.current) {
      midiMessageCallbackRef.current(data);
    }
  };

  const registerMidiMessageHandler = (callback: (data: Uint8Array) => void) => {
    midiMessageCallbackRef.current = callback;
  };

  // üîç H√†m qu√©t thi·∫øt b·ªã BLE (t√πy m√¥i tr∆∞·ªùng)
  const startScan = async () => {
    if (connectedDevice) return;
    setScanResults([]);
    logMsg("üîé Scanning for MIDI devices...");

    try {
      // üì± N·∫øu ƒëang trong app Capacitor (Android/iOS)
      if (Capacitor.isNativePlatform()) {
        await BleClient.requestLEScan(
          { services: [MIDI_SERVICE_UUID] },
          (result) => {
            if (result.device && result.rssi > -90) {
              setScanResults((prev) => {
                if (!prev.find((d) => d.deviceId === result.device.deviceId)) {
                  logMsg(`üì° Found: ${result.device.name || result.device.deviceId}`);
                  return [...prev, result.device];
                }
                return prev;
              });
            }
          }
        );

        setTimeout(async () => {
          await BleClient.stopLEScan();
          logMsg(`üõë Scan stopped (${scanResults.length} devices).`);
        }, 5000);
      }

      // üåê N·∫øu ƒëang tr√™n web
      else if ("bluetooth" in navigator) {
        const device = await navigator.bluetooth.requestDevice({
          filters: [{ services: [MIDI_SERVICE_UUID] }],
          optionalServices: [MIDI_SERVICE_UUID],
        });

        logMsg(`üéπ Selected device: ${device.name || "Unknown"}`);

        // G√°n v√†o scanResults ƒë·ªÉ hi·ªÉn th·ªã
        setScanResults([
          {
            deviceId: device.id,
            name: device.name || "Unknown",
          } as any,
        ]);

        // K·∫øt n·ªëi qua Web Bluetooth
        const server = await device.gatt?.connect();
        const service = await server?.getPrimaryService(MIDI_SERVICE_UUID);
        const characteristic = await service?.getCharacteristic(
          MIDI_CHARACTERISTIC_UUID
        );

        await characteristic?.startNotifications();
        characteristic?.addEventListener("characteristicvaluechanged", (event) => {
          const value = (event.target as BluetoothRemoteGATTCharacteristic).value;
          if (!value) return;
          handleMidiMessage(new Uint8Array(value.buffer));
        });

        setConnectedDevice({ deviceId: device.id, name: device.name } as any);
        logMsg("‚úÖ Connected via Web Bluetooth!");
      } else {
        logMsg("‚ùå BLE not supported on this platform.");
      }
    } catch (err: any) {
      logMsg("‚ö†Ô∏è Scan error: " + err.message);
    }
  };

  const connectDevice = async (device: Device) => {
    if (!Capacitor.isNativePlatform()) return;
    logMsg(`üîå Connecting to ${device.name || device.deviceId}...`);
    try {
      await BleClient.connect(device.deviceId, () => disconnect());
      await BleClient.startNotifications(
        device.deviceId,
        MIDI_SERVICE_UUID,
        MIDI_CHARACTERISTIC_UUID,
        (value) => {
          handleMidiMessage(new Uint8Array(value.buffer));
        }
      );
      setConnectedDevice(device);
      logMsg(`‚úÖ Connected to ${device.name || device.deviceId}`);
    } catch (e: any) { logMsg(`‚ö†Ô∏è Connection error: ${e.message}`); }
  };

  const disconnect = async () => {
    if (connectedDevice) {
      try {
        if (Capacitor.isNativePlatform()) {
          await BleClient.disconnect(connectedDevice.deviceId);
        } else {
          // Web Bluetooth
          (navigator as any).bluetooth?.getDevices?.().then(async (devices: any[]) => {
            for (const d of devices) {
              if (d.id === connectedDevice.deviceId && d.gatt?.connected) {
                await d.gatt.disconnect();
              }
            }
          });
        }
        logMsg(`üîå Disconnected: ${connectedDevice.name}`);
      } catch (e: any) {
        logMsg("‚ö†Ô∏è Disconnect error: " + e.message);
      }
      setConnectedDevice(null);
    }
  };

  return (
    <div style={{ padding: "20px" }}>
      {connectedDevice ? (
        <PianoTutorial file="/alphatab/Canon_in_D.gp" onMidiMessage={registerMidiMessageHandler} />
      ) : (
        <div>

      <h1>üéπ Next.js MIDI BLE Scanner</h1>
      <p>
        Tr·∫°ng th√°i:{" "}
        <strong>
          {connectedDevice
            ? connectedDevice.name || connectedDevice.deviceId
            : "Ch∆∞a k·∫øt n·ªëi"}
        </strong>
      </p>

      <button
        onClick={startScan}
        disabled={!!connectedDevice}
        style={{
          padding: "10px",
          backgroundColor: "lightblue",
          border: "none",
          marginRight: "10px",
        }}
      >
        {scanResults.length > 0
          ? `Rescan (${scanResults.length} found)`
          : "Start Scan for Piano MIDI"}
      </button>

      <button
        onClick={disconnect}
        disabled={!connectedDevice}
        style={{
          padding: "10px",
          backgroundColor: "salmon",
          border: "none",
        }}
      >
        Disconnect
      </button>

      <hr style={{ margin: "20px 0" }} />

      <h2>T√¨m th·∫•y ({scanResults.length})</h2>
      <ul style={{ listStyleType: "none", padding: 0 }}>
        {scanResults.map((device) => (
          <li key={device.deviceId} style={{ marginBottom: "5px" }}>
            <strong>{device.name || "Unknown Device"}</strong> ({device.deviceId})
            {!Capacitor.isNativePlatform() ? null : (
              <button
                onClick={() => connectDevice(device)}
                disabled={!!connectedDevice}
                style={{ marginLeft: "10px", padding: "5px 10px" }}
              >
                Connect
              </button>
            )}
          </li>
        ))}
      </ul>
      </div>
      )}

      <hr style={{ margin: "20px 0" }} />

      <h2>Log</h2>
      <pre
        style={{
          whiteSpace: "pre-wrap",
          backgroundColor: "#eee",
          padding: "10px",
          maxHeight: "300px",
          overflowY: "scroll",
          fontSize: "12px",
        }}
      >
        {log}
      </pre>
    </div>
  );
};

export default MidiPianoScanner;
